<script>
import {loginUrl, userUrl, getHeader} from '../../config'
import vuex from 'vuex'

import toastr from '../../assets/toastr.min.js'
//import homer from '../../assets/home.js'

export default {
  data() {
    return {
      site: {
        nom:  "",
        description:  "",
        image: null,
        imageExt: null
      },
      lat:0,
      lng:0,
      map: true,
      drawingManager: null,
      selectedShape: null,
      pathsWM: [],
      cartLoading: true,
      sending: false
    };
  },
  created () {},
  computed: {},
  ready() {
    console.log("hlkjb")
    setTimeout(() => {
      this.check()
    },1000);
  },
  mounted(){
    console.log("hlkjb")
    setTimeout(() => {
      this.check()
    },1000);
  },
  attached() {},
  methods: {
    check(){
      console.log("test");
      if(typeof google !=='undefined'){
        console.log("test2");
        this.createMap()
      }else{
        //setTimeout(this.check,1000);
      }
    },
    createMap(){
      console.log("test");
      this.cartLoading = false
      var map = new google.maps.Map(document.getElementById('map'), {
          zoom: 8,
          center: new google.maps.LatLng(5.729063, 10.859204),
          mapTypeId: google.maps.MapTypeId.ROADMAP,
          disableDefaultUI: false,
          zoomControl: true
      })
      var polyOptions = {
          strokeWeight: 0,
          fillOpacity: 0.45,
          editable: true,
          draggable: false,
          fillColor: '#0000ff'
      }
    }
  },
  components: {}
};
</script>
<style src="../../assets/home.css" scoped></style>



